<template>
  <div id="app">
   
    <div id="map">
 
    </div>
  </div>
</template>

<script>
import mapboxgl from 'mapbox-gl'; // or "const mapboxgl = require('mapbox-gl');"
var DOMAIN= location.href.indexOf('github.io')!=-1 ? 'https://fengchuan.pythonanywhere.com' : 'http://127.0.0.1:8000';
export default {
	data(){
		return {map:null,popup:null}
	},
	mounted(){
		mapboxgl.accessToken = 'pk.eyJ1IjoiMTI5OTY5Mzk5OSIsImEiOiJja2UyamwyeW4wOXpuMnRueTVxZ3F0dHljIn0.b0lCIK3qG1RySUodJdAB6Q';
		this.map = new mapboxgl.Map({
		container: 'map', // container id
		style: 'mapbox://styles/mapbox/light-v10', // style URL
		center: [-74.5, 40], // starting position [lng, lat]
		zoom: 2 // starting zoom
		});
	
	
	// this.map.addSource('earthquakes', {
	// type: 'geojson',
	// // Point to GeoJSON data. This example visualizes all M1.0+ earthquakes
	// // from 12/22/15 to 1/21/16 as logged by USGS' Earthquake hazards program.
	// data:
	// DOMAIN+'/ethereum/api/getdatas/',
	// cluster: true,
	// clusterMaxZoom: 14, // Max zoom to cluster points on
	// clusterRadius: 50 // Radius of each cluster when clustering points (defaults to 50)
	// });
	
	
		// this.axios.get(DOMAIN+'/ethereum/api/getdatas/').then(ret=>{
			
		// 	this.map.addSource('point', {
		// 	'type': 'geojson',
		// 	'data': ret.data
		// 	});
			 
		// 	this.map.addLayer({
		// 	'id': 'point',
		// 	'type': 'circle',
		// 	'source': 'point',
		// 	'paint': {
		// 	'circle-radius': 3,
		// 	'circle-color': '#3887be'
		// 	}
		// 	});
			
		// });
		this.map.on('load',  ()=> {
		// Add a new source from our GeoJSON data and
		// set the 'cluster' option to true. GL-JS will
		// add the point_count property to your source data.
		
		this.map.addSource('route', {
		'type': 'geojson',
		'data': {
		'type': 'Feature',
		'properties': {},
		'geometry': {
		'type': 'LineString',
		'coordinates': [
		]
		}
		}});
		
		this.map.addLayer({
		'id': 'route',
		'type': 'line',
		'source': 'route',
		'layout': {
		'line-join': 'round',
		'line-cap': 'round'
		},
		'paint': {
		'line-color': 'grey',
		'line-width': 1
		}
		});
		
		this.map.addSource('earthquakes', {
		type: 'geojson',
		// Point to GeoJSON data. This example visualizes all M1.0+ earthquakes
		// from 12/22/15 to 1/21/16 as logged by USGS' Earthquake hazards program.
	
		data:
		DOMAIN+'/ethereum/api/getdatas/',
		cluster: true,
		clusterMaxZoom: 14, // Max zoom to cluster points on
		clusterRadius: 20 // Radius of each cluster when clustering points (defaults to 50)
		});
		 
		this.map.addLayer({
		id: 'clusters',
		type: 'circle',
		source: 'earthquakes',
		filter: ['has', 'point_count'],
		paint: {
		// Use step expressions (https://docs.mapbox.com/mapbox-gl-js/style-spec/#expressions-step)
		// with three steps to implement three types of circles:
		//   * Blue, 20px circles when point count is less than 100
		//   * Yellow, 30px circles when point count is between 100 and 750
		//   * Pink, 40px circles when point count is greater than or equal to 750
		'circle-color': [
		'step',
		['get', 'point_count'],
		'#51bbd6',
		100,
		'#f1f075',
		750,
		'#f28cb1'
		],
		'circle-radius': [
		'step',
		['get', 'point_count'],
		20,
		100,
		30,
		750,
		40
		]
		}
		});
		 
		this.map.addLayer({
		id: 'cluster-count',
		type: 'symbol',
		source: 'earthquakes',
		filter: ['has', 'point_count'],
		layout: {
		'text-field': '{point_count_abbreviated}',
		'text-font': ['DIN Offc Pro Medium', 'Arial Unicode MS Bold'],
		'text-size': 12
		}
		});
		 
		this.map.addLayer({
		id: 'unclustered-point',
		type: 'circle',
		source: 'earthquakes',
		filter: ['!', ['has', 'point_count']],
		paint: {
		'circle-color': 'red',
		'circle-radius': 6,
		'circle-stroke-width': 1,
		'circle-stroke-color': '#fff'
		}
		});
		 
		// inspect a cluster on click
		this.map.on('click', 'clusters', function (e) {
		var features = map.queryRenderedFeatures(e.point, {
		layers: ['clusters']
		});
		var clusterId = features[0].properties.cluster_id;
		this.map.getSource('earthquakes').getClusterExpansionZoom(
		clusterId,
		function (err, zoom) {
		if (err) return;
		 
		this.map.easeTo({
		center: features[0].geometry.coordinates,
		zoom: zoom
		});
		}
		);
		});
		 
		// When a click event occurs on a feature in
		// the unclustered-point layer, open a popup at
		// the location of the feature, with
		// description HTML from its properties.
		this.map.on('click', 'unclustered-point', function (e) {

		});
		 
		this.map.on('mouseenter', 'clusters',  ()=> {
		this.map.getCanvas().style.cursor = 'pointer';
		});
		this.map.on('mouseleave', 'clusters', () =>{
		this.map.getCanvas().style.cursor = '';
		});
		
		this.map.on('mouseenter', 'unclustered-point', (e) =>{

		
			var coordinates = e.features[0].geometry.coordinates.slice();
			let ip = e.features[0].properties.ip;
			let nodeid = e.features[0].properties.id;
			let pongtime=e.features[0].properties.pongtime;
			//var tsunami;
			
			 
			// Ensure that if the map is zoomed out such that
			// multiple copies of the feature are visible, the
			// popup appears over the copy being pointed to.
			while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
			coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
			}
			 if(this.popup){
				 this.popup.remove();
				 this.popup=null;
			 }
			this.popup=new mapboxgl.Popup()
			.setLngLat(coordinates)
			.setHTML(
			`nodeid:${nodeid}<br>ip:${ip}<br>lastpongtime:${pongtime}<br>neighbours:<i id="neighbours">...</i>`
			)
			.addTo(this.map);
			this.axios.get(DOMAIN+'/ethereum/api/getneighbours/'+nodeid+'/'+ip+'/').then(ret=>{
				console.log(1111);
				console.log(ret.data.data);
				document.getElementById('neighbours').innerHTML=ret.data.data;
				this.map.getSource('route').setData(ret.data.json);
				
				
				
			}
			
			);
		
		});
		this.map.on('mouseleave', 'unclustered-point',  () =>{
			if(this.popup){
							 this.popup.remove();
							 this.popup=null;
			}
		
		
		});
		
		});
		
	},
  methods: {
    startHacking () {
      this.$notify({
        title: 'It works!',
        type: 'success',
        message: 'We\'ve laid the ground work for you. It\'s time for you to build something epic!',
        duration: 5000
      })
    }
  }
}
</script>

<style>
	html,body,#app,#map{
		width: 100%;
		height: 100%;
	}
#app {
  font-family: Helvetica, sans-serif;
  text-align: center;
}
</style>



